<template>
  <div id="right">
    <h3>You can change User Information</h3>
    <p>수정사항</p>
    <label for="이름">Name : </label>
    <input type="text" v-model="user.name" /><br />
    <label for="주소">Adress : </label>
    <input type="text" v-model="user.address" /><br />
    <label for="번호">Phone : </label>
    <input type="text" v-model="user.phone" /><br />
    <button @click="changeUser">Complite</button>
  </div>
</template>

<script>
export default {
  props: ["name", "address", "phone"],
  data() {
    return {
      user: {},
    };
  },
  created() {
    // 재할당을 통해 user오브젝트로 넣어줌
    this.user.name = this.name;
    this.user.address = this.address;
    this.user.phone = this.phone;
  },
  methods: {
    changeUser() {
      console.log(this.user);
      this.$emit("child", this.user);
      this.user.name = "";
      this.user.address = "";
      this.user.phone = "";
      // 값이 여러개 일때
      // this.$emit("child", this.user,1,2,3,4);
      // child(this.user,1,2,3,4)
    },
  },
};
</script>
<style scoped>
#right {
  width: 50%;
  background: thistle;
  float: left;
}
input {
  margin: 5px;
}
button {
  margin: 5px;
  background: violet;
  color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}
</style>
